import React, {Component} from 'react';
import { Link, withRouter } from 'react-router-dom'
import {getBaseDataSource,selectPorcessById,savePorcess,downloadProcess,deleteProcess, searchWorkflow} from '../../api/process'
import {deleteExperiment ,stopExperiment, runExperimentRunning} from '../../api/experimets'

import { Table, Divider, Spin,Tag, PageHeader, Modal,  Input,Button,message } from 'antd';

import Trash from './trash'
import ProcessModal from'./processModal'
import ExperimentModal from'./experimentModal'
import ExperimentRunning from './experimentRunning'
import TemplateModal from './templateModal'

import './index.less'

@withRouter 
class Process extends Component{
  constructor(props){
    super(props);
    this.state = {
      dataSource: [],            //-
      runningDataSource:[],      //
      loading: false,            //
      total:1,                   //
      page: 1,                   //
      pageSize:10,               //
      search:'',                 //

      newProcessVisible:false,        //
      cloneProcessVisible:false,      //
      newExperimentsVisible:false,    //
      cloneExperimentsVisible:false,  //
      editorExperimentsVisible:false, //
      markTemplateVisible:false,      //
      runningVisible:false,           //
      trashVisible:false,             //
      
      cloneId:null ,           //id
      processId:null ,         //id    ---
      experimentId:null,       //id    ---
      selectProcessKey:'',     // id-
      selectExperimentKey:null // id 
    }
  }
  componentDidMount(){
    this.getBaseDataSource()
  }
  //
  getBaseDataSource=()=>{
    this.setState({loading: true})
    const params = {
      pageNum: this.state.page,
      pageSize: 10,
      isDeleted:'0',
      userId:2
    }
    const {page} = this.state
    getBaseDataSource(params).then((res)=>{
        if(res){
          if(res.data.meta.code == '001'){
            const dataSource = res.data.data.WorkflowVOList.map((item,index)=>{
              return { ...item, index: index+1+(page-1)*10,key:item.id }
            })
            this.setState({ 
              dataSource, 
              loading: false,
              total: res.data.data.Total
            })
          }else{
            message.error(res.data.meta.message)
          }
        }
      }).catch(err=>{
        console.log(err)
      })
  }
  //message
  showMessage=(res)=>{
    if(res){
      if(res.data.meta.code == '001'){
        message.success(res.data.meta.message)
      }else{
        message.error(res.data.meta.message)
      }
    }
  }
  //
  saveProcess=(record)=>{
    console.log(record)
    const params = {
      workflowId: record.id,
      workflowXml:record.workflowXml,
      ggeditorObjectString : record.ggeditorObejectString
    }
    console.log('')
    console.log(params)
    savePorcess(params).then(res=>{
      this.showMessage(res)
    }).catch(err=>{
      console.log(err)
    })
  }

  // ---
  selectProcess=(record)=>{
    selectPorcessById(record.id).then(res=>{
      console.log(res.data)
    }).catch(err=>{
      console.log(err)
    })
  }
  
  // 
  downloadProcess=(id)=>{
    downloadProcess(id).then(res=>{
      console.log(res)
      // if(res){
      //   var blob = new Blob([res.data])
      //   console.log(blob)
      //   var href = window.URL.createObjectURL('');
      //   var link = document.createElement('a');
      //   link.setAttribute("download", 'pixture');
      //   link.href = href;
      //   link.click();
    // }
    }).catch(err=>{
      console.log(err)
    })
  }
  //
  handleSearch = value => {
    let that = this
    this.setState({
      page: 1
    }, function(){
      that.getSearchData(value)      
    })
  }
  //
  changePage = () => {
    const { search } = this.state
    search ? this.getSearchData(search) : this.getBaseDataSource()
  }
  //
  getSearchData=(value)=>{
    let params={
      userId:2,
      workflowName : value,
      pageNum: this.state.page,
      pageSize: 10,
    }
    const {page}=this.state
    searchWorkflow(params).then(res=>{
      if(res){
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data.WorkflowVOList.map((item,index)=>{
            return { ...item, index: index+1+(page-1)*10,key:item.id}
          })
          this.setState({ 
            dataSource, 
            loading: false,
            total: res.data.data.Total
          })
        }else{
          message.error(res.data.meta.message)
          this.setState({ loading: false })
        }
      }
    }).catch(err=>{
      this.setState({ loading: false })
      console.log(err)
    })
  }
  // 
  cloneProcess=(id)=>{
    this.setState({
      cloneProcessVisible:true,
      cloneId:id
    })
  }
  //
  newExperiments=(id)=>{
    this.setState({
      newExperimentsVisible:true,
      processId:id
    })
  }
  //
  deleteProcess=(id)=>{
    console.log(''+id);
    const params={
      workflowId:id
    }
    deleteProcess(params).then(res=>{
      console.log('')
      this.showMessage(res)
      this.getBaseDataSource()
    }).catch(err=>{
      console.log(err)
    })
  }
  //
  editorExperiment=(id)=>{
    this.setState({
      editorExperimentsVisible:true,
      experimentId:id
    })
  }
  //
  deleteExperiment=(id)=>{
    const params={
      experimentId:id
    }
    deleteExperiment(params).then(res=>{
      console.log(''+id)
      this.showMessage(res)
      this.getBaseDataSource()
    }).catch(err=>{
      console.log(err)
    })
  }
  //
  runExperiment=(id)=>{
    const params={
      experimentId:id
    }
    runExperimentRunning(params).then(res=>{
      console.log(''+id)
      this.showMessage(res)
    }).catch(err=>{
      console.log(err)
    })
  }
  //
  stopExperiment=(id)=>{
    const params={
      experimentId:id
    }
    stopExperiment(params).then(res=>{
      console.log(''+id)
      this.showMessage(res)
    }).catch(err=>{
      console.log(err)
    })
  }
  
  //
  cloneExperiment=(id)=>{
    this.setState({
      cloneExperimentsVisible:true,
      experimentId:id
    })
  }

  render(){
    const { dataSource, newProcessVisible, cloneProcessVisible, newExperimentsVisible,    
      cloneExperimentsVisible,  editorExperimentsVisible,   markTemplateVisible,     
      runningVisible,  trashVisible,    } = this.state
   
    //
    const columns = [
      {title: 'id', dataIndex: 'id', key: 'id',},
      {title: '',dataIndex: 'name',key: 'name',
        render:(text,record)=>(
          <a onClick={()=>this.selectProcess(record)}>{text}</a>
        )
      },
      {title: '',dataIndex: 'tags', key: 'tags',
        render:tags=>(
          <>
            {tags.split(',').map((item,index)=>(
              <Tag color='orange' key={index}>{item}</Tag>
            ))}
          </>
        )},
      { title: '',dataIndex: 'createTime', key: 'createTime',},
      {title: '',dataIndex: 'workflowDesc', key: 'workflowDesc',},
      {title: '',key: 'action',align:'center',width:'200px',
        render: (text, record) => (
          <span>
            <a onClick={()=>{this.cloneProcess(record.id)}}></a>
            {/* <Divider type="vertical" />
            <a onClick={()=>{this.saveProcess(record)}}></a> */}
            {/* <Divider type="vertical" />
            <a onClick={()=>{this.downloadProcess(record.id)}}></a> */}
            <Divider type="vertical" />
            <a onClick={()=>{this.newExperiments(record.id)}}></a>
            <Divider type="vertical" />
            <a onClick={()=>{this.deleteProcess(record.id)}}></a>
          </span>
        )},
    ];

    //
    const experimentsColumns = [
      { title: 'id', dataIndex: 'id', key: 'id' },
      {title: '',dataIndex: 'name',key: 'name',
        render:(text,record)=>(
          <span style={{color:this.state.selectExperimentKey===record.id?'red':''}}>{text}</span>
        )
      },
      { title: '', dataIndex: 'createTime', key: 'createTime' },
      { title: '', dataIndex: 'experimentDesc', key: 'experimentDesc'},
      {
        title: '', align:'center',width:'350px',
        dataIndex: 'action',
        key: 'action',
        render: (text,record) => (
          <span>
            <a onClick={()=>this.runExperiment(record.id)}></a>
            <Divider type='vertical'/>
            <a onClick={()=>this.stopExperiment(record.id)}></a>
            <Divider type='vertical'/>
            <a onClick={()=>this.editorExperiment(record.id)}></a>
            <Divider type='vertical'/>
            <a onClick={()=>this.deleteExperiment(record.id)}></a>
            <Divider type='vertical'/>
            <a onClick={()=>this.setState({runningVisible:true,experimentId:record.id})}></a>
            <Divider type='vertical'/>
            <a onClick={()=>this.cloneExperiment(record.id)}></a>
            <Divider type='vertical'/>
            <a onClick={()=>this.setState({markTemplateVisible:true,experimentId:record.id})}></a>
          </span>
        ),
      },
    ];

    return(
    <div className="processWrapper">
      <div className="processContent">
        <div className="listWrapper">
          <PageHeader
            style={{border: '1px solid rgb(235, 237, 240)', marginBottom: '19px'}}
            title={<span onClick={this.getBaseDataSource}>-</span>}
            extra={[
                <Input.Search style={{width:'200px'}} onSearch={this.handleSearch} placeholder="" />,
                <Button type='primary' onClick={()=>this.setState({trashVisible:true})}></Button>,
                <Button type='primary' onClick={()=>{this.setState({newProcessVisible:true})}}></Button>,
            ]}
          />
          <Spin spinning={this.state.loading} tip="loading...">
            <Table 
              size='small' bordered
              columns={columns}
              defaultExpandedRowKeys={this.state.selectProcessKey}
              expandedRowRender={
                record=>(record.experimentList.length !== 0
                  ? <Table columns={experimentsColumns} dataSource={record.experimentList} pagination={false} />
                  : null)}
              dataSource={dataSource}
              pagination={{
                onChange: page => {
                  console.log(page);
                  this.setState({page},()=>{
                    this.changePage()
                  });
                },
                current:this.state.page,
                pageSize: 10,
              }}
            />
          </Spin>
        </div>
      </div>
      {/* modal */}
      {newProcessVisible&&
      <Modal
        title=""
        visible={newProcessVisible}
        footer={null}
        onCancel={()=>{this.setState({newProcessVisible:false})}}
      >
        <ProcessModal close={()=>{
          this.setState({newProcessVisible:false})
          this.getBaseDataSource()
          }}/>
      </Modal>}

      {cloneProcessVisible&&
      <Modal
        title=""
        visible={cloneProcessVisible}
        footer={null}
        onCancel={()=>{this.setState({cloneProcessVisible:false,cloneId:null})}}
      >
        <ProcessModal 
          cloneId={this.state.cloneId}
          close={()=>{
            this.setState({cloneProcessVisible:false})
            this.getBaseDataSource()
          }}
          isClone={true} 
        />
      </Modal>
      }

      {newExperimentsVisible&&
      <Modal
        title=""
        visible={newExperimentsVisible}
        footer={null}
        onCancel={()=>{this.setState({newExperimentsVisible:false})}}
      >
        <ExperimentModal
          processId={this.state.processId}
          close={()=>{
            this.setState({newExperimentsVisible:false})
            this.getBaseDataSource()
          }}
          modalType={'create'}
        />
      </Modal>}

      {editorExperimentsVisible&&
      <Modal
        title=""
        visible={editorExperimentsVisible}
        footer={null}
        onCancel={()=>{this.setState({editorExperimentsVisible:false,experimentId:null})}}
      >
        <ExperimentModal
          close={()=>{
            this.setState({editorExperimentsVisible:false,experimentId:null})
            this.getBaseDataSource()
          }}
          experimentId={this.state.experimentId}
          modalType={'editor'}
        />
      </Modal>}

      {cloneExperimentsVisible&&
      <Modal
        title=""
        visible={cloneExperimentsVisible}
        footer={null}
        onCancel={()=>{
          this.setState({cloneExperimentsVisible:false,experimentId:null})
          this.getBaseDataSource()
        }}
      >
        <ExperimentModal
          close={()=>{
            this.setState({cloneExperimentsVisible:false,experimentId:null})
            this.getBaseDataSource()
          }}
          experimentId={this.state.experimentId}
          modalType={'clone'}
        />
      </Modal>}

      {runningVisible&&
      <Modal
        title="" width='600px'
        visible={runningVisible}
        footer={null}
        onCancel={()=>{this.setState({runningVisible:false,experimentId:null})}}
      >
        <ExperimentRunning 
          experimentId={this.state.experimentId}
          close={()=>{
            this.setState({runningVisible:false,experimentId:null})
            this.getBaseDataSource()
          }}
        />
      </Modal>}

      {markTemplateVisible&&
      <Modal
        title=""
        visible={this.state.markTemplateVisible}
        footer={null}
        onCancel={()=>{this.setState({markTemplateVisible:false,experimentId:null})}}
      >
        <TemplateModal 
          experimentId={this.state.experimentId}
          close={()=>{
            this.setState({markTemplateVisible:false,experimentId:null})
            this.getBaseDataSource()
            }}/>
      </Modal>}

      {trashVisible&&
      <Modal
        title=""
        visible={trashVisible}
        footer={null}
        onCancel={()=>{
          this.setState({trashVisible:false})
          this.getBaseDataSource()
        }}
      >
        <Trash />
      </Modal>}
    </div>
    )
  }
}
export default Process;
